.navbar-container {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 6;
    width: 100%;
    transition: 0.2s; }


.nav-active {
    border-radius: 50%;
    border: 1px solid $pink; }

.nav-shadow {
    box-shadow: 0px 4px 10px -4px $pink;
    transition: 0.2s;
    background-color: white; }

@media only screen and (max-width: 990px) {
    .pages-nav {
        position: absolute;
        top: 0;
        left: 0;
        background-color: white;
        box-shadow: -9px 5px 12px -4px $pink; } }
